:root {
  --backgroundColour: #221F2D;
  --backgroundColourSecondary: #242130;
  --contentBackgroundColour: #353549;
  --textColour: #fff;
  --textColourSecondary: #f2f2f2;
  --headerFont: 'roboto', helvetica, arial;
  --bodyFont: 'roboto', helvetica, arial;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/* Columns */
.col {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  height: 100vh;
}

.header {
  width: 100%;
  background: var(--contentBackgroundColour);
}

label {
  display: block;
  margin-top: 2em;
  text-align: left;
}

input {
  display: block;
  background: transparent;
  font-size: 1rem;
  border: solid 1.5px #fff;
  border-radius: 5px;
  line-height: 1.5;
  padding: 0.5em 1em;
  color: var(--textColourSecondary);
  font-weight: 300;
  margin-top: 2em;
  width: 100%;
}

input.valid {
  border: solid 1.5px #82DDA9;
}

input.invalid {
  border: solid 1.5px #ff6644;
}

.btn {
  display: inline-block;
  color: var(--contentBackgroundColour);
  line-height: 1.5;
  font-size: 1rem;
  padding: 0.5em 1em 0.4em;
  background: #fff;
  border: solid 1.5px #fff;
  cursor: pointer;
  font-weight: bold;
}

.submit-btn {
  margin-top: 0.5em;
  border-radius: 5px;
}

.delete-btn {
  position: absolute;
  top: -5px;
  border-radius: 2px;
  right: -10px;
  padding: 0 0.5em;
  background: #fff;
  border: 0;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: opacity 0.15s;
  -webkit-transition: opacity 0.15s;
  -o-transition: opacity 0.15s;
  transition: opacity 0.15s;
  -webkit-box-shadow: 0 2px 6px var(--backgroundColour), 0 1px 3px var(--backgroundColour);
  box-shadow: 0 2px 6px var(--backgroundColour), 0 1px 3px var(--backgroundColour);
}

.colour:hover .delete-btn {
  opacity: 1;
  pointer-events: auto;
}

h2 {
  font-weight: 300;
}

ul {
  display: -ms-flexbox;
  display: flex;
}

.colour-list {
  display: grid;
  grid-template-columns: repeat(4, minmax(120px, 150px));
  grid-column-gap: 2.5em;
  grid-row-gap: 2.5em;
  position: relative;
}

.colours {
  margin-left: 2.5rem;
}

.invisible {
  opacity: 0;
  -webkit-transform: rotateX(-90deg);
  transform: rotateX(-90deg);
}

.hidden {
  display: none;
}

.mouse-enabled *:focus {
  outline: none;
}

.colour-add {
  opacity: 0;
  text-align: left;
  width: 100%;
  background: none;
  border: dashed var(--contentBackgroundColour) 1.5px;
  color: var(--contentBackgroundColour);
  font-size: 5rem;
  text-align: center;
  height: 205px;
  cursor: pointer;
  -webkit-transition: all 0.25s;
  -o-transition: all 0.25s;
  transition: all 0.25s;
}

.colour-add:hover {
  color: var(--textColour);
  border: dashed 1.5px var(--textColour);
}

.colour-list:hover .colour-add {
  opacity: 1;
}

.loader {
  position: absolute;
  top: 1em;
  left: 0;
  right: 0;
  margin: auto;
  width: 75px;
  height: 16px;
  text-align: center;
}

.loader__dot {
  width: 12px;
  height: 12px;
  background: #fff;
  border-radius: 50%;
  float: left;
  margin: 0 5px;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
      transform: scale(0);
  -webkit-animation: fx 800ms ease infinite 0ms;
  animation: fx 800ms ease infinite 0ms;
}

.loader__dot:nth-child(2) {
  -webkit-animation: fx 800ms ease infinite 200ms;
  animation: fx 800ms ease infinite 3s;
}

.loader__dot:nth-child(3) {
  -webkit-animation: fx 800ms ease infinite 400ms;
  animation: fx 800ms ease infinite 600ms;
}

@-webkit-keyframes fx {
  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fx {
  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}



